export default {
	
	props: {
		title: {
			type: String,
			default: '提示'
		},
		message: {
			type: String,
			default: '123'
		},
		// 内容对齐方式 center | left | right
		messageAlign: {
			type: String,
			default: 'center'
		},
		// 是否显示确认按钮
		showConfirmButton: {
			type: Boolean,
			default: true
		},
		// 是否显示取消按钮
		showCancelButton: {
			type: Boolean,
			default: false
		},
		// 确定按钮的文案
		confirmButtonText: {
			type: String,
			default: '确定'
		},
		// 取消按钮的文案
		cancelButtonText: {
			type: String,
			default: '取消'
		},
		// 是否显示蒙层
		overlay: {
			type: Boolean,
			default: true
		},
		// 点击蒙层是否关闭弹窗
		closeOnClickOverlay: {
			type: Boolean,
			default: false
		}
	},
	
	data() {
		return {
			isVisibility: false,
			animationData:{}
		};
	},
	
	methods: {
		__show () {
			this.isVisibility = true
		},
		__close () {
			this.isVisibility = false
		},
		alert (option) {				
			this.__show()
			this.showCancelButton = false
			
			if(Object.keys(option).length !=0 ) {
				this.title = option.title
				this.message = option.message
				this.overlay = option.overlay
				this.closeOnClickOverlay = option.closeOnClickOverlay
			}
		},
		confirm (option) {

			this.showCancelButton = true
			this.__show()
			console.log(option)
			if(Object.keys(option).length !=0 ) {
				this.title = option.title
				this.message = option.message
				this.overlay = option.overlay
				this.closeOnClickOverlay = option.closeOnClickOverlay
			}
		},
		handleClose () {
			this.__close()
			this.$emit('cancelButton')
		},
		handleConfirm () {
			this.__close()
			this.$emit('confirmButton')
		}
	}
	
}
